<script>
import axios from 'axios'

export default {
  data() {
    return {
      // product: null,
      // old stuff
      sizeguide: false,
      ad: localStorage.getItem('ad'),
     cartNotificationVisible: false,
      adinfo: false,
      selectedImage: null,
      currentButton: null,
      cartLength: 0, // Add this property
      cart: [],
      myItems: [
        { name: 'Item 1', price: 10 },
        { name: 'Item 2', price: 20 },
        { name: 'Item 3', price: 30 },
      ],
      product: {},
      buttons: [
        { id: 1, label: "8" },
        { id: 2, label: "10" },
        { id: 4, label: "12" },
        { id: 5, label: "14" },
        { id: 6, label: "16" },
        { id: 7, label: "18" },
        { id: 8, label: "20" },
      ],
      sizes: [
        {
          nop: localStorage.getItem('title'),
        },
        {
          price: localStorage.getItem('price'),
        },
        {
          clsize: ""
        },
        {
          quantity: 0,
        },
        {
          img: localStorage.getItem('image'),
        },
      ],
    }
  },

  mounted() {
    const storedProduct = localStorage.getItem('product');
    if (storedProduct) {
      this.product = JSON.parse(storedProduct);
    } else {
      axios
        .get('lt.php')
        .then((response) => {
          const { title, price, image, imagetwo, imagethree, description, info, meaning, additionalinfo } = response.data[0];

          this.product.title = title;
          this.product.price = price;
          this.product.image = image;
          this.product.imagetwo = imagetwo;
          this.product.imagethree = imagethree;
          this.product.description = description;
          this.product.info = info;
          this.product.meaning = meaning;
          this.product.additionalinfo = additionalinfo;
          this.selectedImage = this.product.image;

          localStorage.setItem('title', this.product.title);
          localStorage.setItem('price', this.product.price);
          localStorage.setItem('image', this.product.image);
          localStorage.setItem('imagetwo', this.product.imagetwo);
          localStorage.setItem('imagethree', this.product.imagethree);
          localStorage.setItem('description', this.product.description);
          localStorage.setItem('info', this.product.info);
          localStorage.setItem('meaning', this.product.meaning);
          localStorage.setItem('additionalinfo', this.product.additionalinfo);

        })
        .catch((error) => {
          console.error(error);
        });
    }
  },

  methods: {
    selectImage(index) {
      const images = [this.product.image, this.product.imagetwo, this.product.imagethree];
      this.selectedImage = images[index];
    },

    showsizeguide() {
      this.sizeguide = true;
    },

    showadinfoguide() {
      this.adinfo = true;
    },

    hidesizeguide() {
      this.sizeguide = false;
    },

    selectImage(index) {
      this.selectedImage = this.images[index];
    },

    // main oh guy
    addtocart() {
      const newItem = { sizes: { ...this.sizes } };
      let cart = JSON.parse(localStorage.getItem('cart')) || [];

      cart.push(newItem);

      localStorage.setItem('cart', JSON.stringify(cart));

      this.cart = cart;
      this.cartLength = cart.length; // Update cartLength with the new
      console.log(this.cart);

      this.cartNotificationVisible = true;
      setTimeout(() => {
        this.cartNotificationVisible = false;
      }, 2300); // Hide notification after 3 seconds
    },
    // end of main

    increasevalue() {
      this.sizes[3].quantity = ++document.getElementById('vi').value;
      console.log(this.sizes);
    },

    decreasevalue() {
      this.sizes['quantity'] = (document.getElementById('vi').value--) || 1;
      if (document.getElementById('vi').value <= 1) {
        document.getElementById('vi').value = 1;
      }
      console.log(this.sizes);
    },

    handleClick(label) {
      this.currentButton = label;
      this.sizes[2].clsize = label;
    },

    // handleClick(btnn) {
    //   this.sizes[2].clsize = btnn;
    //   // console.log(this.sizes)
    // },

    changeColor() {
      this.currentBorderColor = this.currentBorderColor === "transparent" ? '#26A76B' : "transparent";
      if (this.selectedItems.length === 0) {
        // Add item
        this.selectedItems.push(this.s);
      } else {
        this.selectedItems.splice(this.s);
      }
      console.log(this.selectedItems);
    },
  },

  computed: {
    formattedAd() {
      const ad = localStorage.getItem('ad');
      if (ad) {
        return ad.split('. ');
      } else {
        return [];
      }
    },
    formatteddesc() {
      const desc = localStorage.getItem('description');
      if (desc) {
        return desc.split('. ');
      } else {
        return [];
      }
    }
  },
}


</script>

