<template>

  <div class="filterheadd">
    <p> styledbyjm </p>
    <h1> LOOKBOOK </h1>
  </div>

 <div class="image-gallery">
      <div class="image-grid" v-for="(image, index) in images" :key="index" @click="openOverlay(image)">
        <img :src="image.src" alt="styledbyjm">
      </div>
    </div>

    <div v-if="showOverlay" class="overlay">
      <div class="overlay-content">
        <h3 class="xtoquit" @click="removeol"> X </h3>
        <br/>
        <img v-if="selectedImage" :src="selectedImage.src" alt="styledbyjm">
        <button class="arrow left" @click="previousImage">&lt;</button>
        <button class="arrow right" @click="nextImage">&gt;</button>
      </div>
    </div>

</template>

<script>
import image1 from '../assets/images/IMG_3944.jpg'
import image2 from '../assets/images/IMG_2258.jpg'
import image3 from '../assets/images/pinkimg2.jpg'
import image4 from '../assets/images/IMG_3943.jpg'
import image5 from '../assets/images/dcbg.jpg'
import image6 from '../assets/images/IMG_2481.jpg'
import image7 from '../assets/images/DSC_2506.jpg'
import image8 from '../assets/images/IMG_3940black.jpg'
import image9 from '../assets/images/IMG_2370w.jpg'
import image10 from '../assets/images/IMG_2467.jpg'
import image11 from '../assets/images/IMG_2356.jpg'
import image12 from '../assets/images/IMG_3941.jpg'
import image13 from '../assets/images/DSC_2438.jpg'
import image14 from '../assets/images/DSC_2486.jpg'
import image15 from '../assets/images/DSC_2457.jpg'
import image16 from '../assets/images/IMG_2269.jpg'
// import image1 from '../assets/images/dcbg.jpg'
// import image2 from '../assets/images/pinkimg1.jpg'
// import image3 from '../assets/images/pinkimg2.jpg'
// import image4 from '../assets/images/dcbg.jpg'
export default {
  data() {
    return {
      images: [
        { src: image1 },
        { src: image2 },
        { src: image3 },
        { src: image4 },
        { src: image5 },
        { src: image6 },
        { src: image7 },
        { src: image8 },
        { src: image9 },
        { src: image16 },
        { src: image11 },
        { src: image12 },
        { src: image13 },
        { src: image14 },
        { src: image15 },
        { src: image10 },
        // Add more images as needed
      ],
      showOverlay: false,
      selectedImage: null,
    };
  },
  methods: {
    removeol(){
        this.showOverlay= false;
    },
    openOverlay(image) {
      this.selectedImage = image;
      this.showOverlay = true;
    },
    previousImage() {
      const currentIndex = this.images.indexOf(this.selectedImage);
      const previousIndex = (currentIndex - 1 + this.images.length) % this.images.length;
      this.selectedImage = this.images[previousIndex];
    },
    nextImage() {
      const currentIndex = this.images.indexOf(this.selectedImage);
      const nextIndex = (currentIndex + 1) % this.images.length;
      this.selectedImage = this.images[nextIndex];
    },
  },
};
</script>
