  margin-top:40px;
  margin-bottom:250px;
}

/* .image-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 10px;
} */

.image-grid img {
  width: 100%;
  height: auto;
  cursor: pointer;
}

.xtoquit{
    margin:auto;
    color:white;
    text-align:center;
    cursor:pointer;
}

.overlay {
  position:fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.overlay-content {
  position: relative;
  /* max-width: 80%; */
  width:60%;
  /* max-height: 50%; */
}

.overlay img {
    width: 50%;
  /* height: auto; */
  display: block;
  margin: 0 auto;
}

.arrow {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 30px;
  padding: 10px;
  cursor: pointer;
}

.arrow.left {
  left: 10px;
}

.arrow.right {
  right: 10px;
}

