<script>

export default {
  props: ['viewProduct'],
  data() {
    return {
      items: [],
    };
  },
  mounted() {
    fetch('pants.php?categories=Pants')
      .then((response) => response.json())
      .then((data) => {
        this.items = data;
        console.log(this.items)
      })
      .catch((error) => {
        console.error('Error fetching items:', error);
      });
  },
};
</script>

